.App {
	text-align: center;
}

.box {
	padding: 0.5em;
	width: 100%;
	margin: 1em;
	border-radius: 8px;
	min-width: 50px;
}
.box.confirmed {
	border: 1px solid rgb(240, 128, 128);
	background: rgb(226, 139, 107);
}
.box.recovered {
	border: 1px solid green;
	background: rgb(150, 226, 150);
}
.box.casualty {
	border: 1px solid rgb(88, 2, 2);
	background: rgb(170, 16, 16);
}
.container {
	padding: 1em;
	width: 100%;
	text-align: center;
}
.flex {
	display: flex;
	justify-content: space-evenly;
	flex-wrap: wrap;
}

select {
	outline: 0;
	background: rgb(161, 211, 240);
	width: 100%;
	height: 100%;
	color: black;
	cursor: pointer;
	border: 1px solid black;
	border-radius: 3px;
}
.dropdown {
	padding: 0.5em;
	margin: 1em;
	width: 30em;
	height: 2.8em;
	line-height: 3;
	overflow: hidden;
	border-radius: 0.25em;
	padding-bottom: 10px;
}
.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media screen and (min-width: 900px) {
	.flex {
		flex-wrap: nowrap;
	}
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}
